import React from 'react'
import { Field, reduxForm } from 'redux-form'


const onSubmit = (values) => {
  console.log(values);
}

const SimpleForm = props => {
  const { handleSubmit, pristine, reset, submitting } = props
  return (
    <div>
      <div>
        <label>First Name</label>
        <div>
          <Field
            name="firstName"
            component="input"
            type="text"
            placeholder="First Name"
          />
        </div>
      </div>
      <div>
        <label>Last Name</label>
        <div>
          <Field
            name="lastName"
            component="input"
            type="text"
            placeholder="Last Name"
          />
        </div>
      </div>
      <div>
        <label>Email</label>
        <div>
          <Field
            name="email"
            component="input"
            type="email"
            placeholder="Email"
          />
        </div>
      </div>
      
      <div>
    <label>Rating</label>
    <div>
        <label>
            <Field name="1" component="input" type="radio" value="1" />
            <span>1</span>
        </label>
        <label>
            <Field name="2" component="input" type="radio" value="2" />
            <span>2</span>
        </label>
        <label>
            <Field name="3" component="input" type="radio" value="3" />
            <span>3</span>
        </label>
        <label>
            <Field name="4" component="input" type="radio" value="4" />
            <span>4</span>
        </label>
        <label>
            <Field name="5" component="input" type="radio" value="5" />
            <span>5</span>
        </label>
    </div>
</div>
     
      <div>
        <label>Notes</label>
        <div>
          <Field name="comment" component="textarea" />
        </div>
      </div>
      <div>
        <button type="submit" onClick={handleSubmit(onSubmit)} disabled={pristine || submitting}>
          Submit
        </button>
        <button type="button" disabled={pristine || submitting} onClick={reset}>
          reset
        </button>
      </div>
    </div>
  )
}

export default reduxForm({
  form: 'simple'
})(SimpleForm)